
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
components: {
XHeader,
Toast
},
data() {
return {
noData: false,
pageIndex: 1,
list: [],
isloading: false,
curIndex: 0
};
},
mounted() {
this.initData(true);
},
methods: {
starActivity(JBSCID) {
this.$router.push({
name: "NewbornScreeningDetail",
params: { JBSCID: JBSCID }
});
},
/**
* @param upOrDown
* true 第一次加载或者刷新
* false 上拉加载
*/
initData(upOrDown) {
if (upOrDown) {
this.pageIndex = 1;
} else {
this.pageIndex++;
}

this.$ajax
.post("", {
"MSH.1": "childrenDiseaseTestService",
"MSH.2": "list",
"PAGE": this.pageIndex,
"PAGE_SIZE": 10,
'GRDAID':this.$root.loginInfo.grdaid,
'CHZBM':this.$root.loginInfo.CHZBM,
'token':this.$root.loginInfo.token,
})
.then(obj => {
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
if (typeof obj != "undefined" && obj.rows.length > 0) {

this.list = this.list.concat(obj.rows);
} else {
if (this.pageIndex > 1) {
this.$vux.toast.text("没有更多了", "bottom");
this.$refs.scroll.finishPullUp();
} else {
this.noData = true;
}
}
})
.catch(error => {
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
console.log(error);
this.noData = true;
});
}
}
};



